/* Backend Settings */

.backend-settings {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 30px;
  background: $color1;
  padding: 0 30px 0 30px;
  margin-bottom: 30px;
  margin-top: -15px;
  background: rgba(60, 63, 65, 0.8);

  // select font size

  input[data-ref="inputEl"] {
    font-size: 13px;
  }

  // Sub Settings Border

  .sub-setting {
    &:last-child {
      border-right: none;
    }
  }

}

.backend-settings--widget {
  margin-top: 0;
  margin-bottom: 0;
}

.backend-settings--wide {
  display: block;
}

.sub-setting {
  //outline: 1px solid red;
  display: grid;
  grid-template-columns: auto 1fr;
  border-right: 1px solid rgba($white, 0.2);
  padding-top: 10px;
  padding-bottom: 3px;
  //grid-template-columns: repeat(auto-fit, minmax(auto, 300px));

  &.checkbox {
    grid-template-columns: 1fr auto;
  }

  input[type='checkbox'] {
    margin-top: 10px;
  }

  label {
    padding-right: 10px;
    margin: 0;
    font: 400 13px 'Open Sans', 'Helvetica Neue', helvetica, arial, verdana, sans-serif;
    line-height: 32px;
    color: rgba($white, 0.5);
  }

}


/* ----------------------------------------------------------------------- */

/* PAGE HEADER */

.page-header {
  height: 800px !important;
  display: block;
  position: relative;
}
.subpage {
  .page-header {
    height: 500px !important;
  }
}

/* HeaderImage & Boxed */

/* Page Header Image */
  .page-header--image-relation {
    position: relative;
    z-index: 9999;
    padding: 30px 20px 20px;
  }


.page-header--image {
  .pimcore_tag_droptarget_upload {
    right: auto;
    left: 0;
    z-index: 1;
  }
}
.page-header--cta {
  max-width: 50%;
  .pimcore_editable:focus {
    text-transform: none !important;
  }
}
.page-header--widgets {
  display: block;
  position: relative;
  z-index: 9999;
  width: 600px;
  padding-left: 20px;
  .page-header--widgets-container {
  }
  .backend-settings--widget {
    display: block;
    .sub-setting {
      grid-template-columns: 1fr auto;
      label {
        text-align: right;
      }
    }
  }
}

/* CTA */

body {
  .page-header--cta {
    z-index: 99;
    max-width: 100%;
  }
}

