/* Images */

.contentblock--textbox figure {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.contentblock figure > div {
  width: 100% !important;
  height: 100% !important;
}

/* Contentblocks */

.contentblock {
  padding-bottom: 30px !important;
  margin-bottom: 0 !important;
}

/* Largeteaser */

.contentblock--largeteaser {

  .largeteaser--image {
    position: relative;
  }

  .largeteaser {
    .arrow-link {
      position: relative;
      right: auto;
      bottom: auto;
    }
  }

}

/* StÃ¶rer */

.backend-cols {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 30px;
}

/* Grid */

.contentblock--grid {
  .grid--image {
    figure {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  .pimcore_tag_droptarget_upload {
    z-index: 99;
  }
}

/* Parallax */

.contentblock--parallax {
  background-color: rgba($color1, .3);
  .parallaxImage {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
    }
  }
  .pimcore_tag_droptarget_upload {
    right: auto;
    left: 0;
    z-index: 1;
  }
}



/* Metanav */

.page-header--meta {
  background: $color2;
}


/* Gallery */

.gallery--container {
  display: block;

  .gallery--image {
    display: inline-block;
    width: 150px;
    height: 150px;

    img {
      width: 150px;
      height: 150px;
    }

  }

}

/* Cols */

.cols {
  display: block !important;
  .backend-settings {
    background: rgba(60, 63, 65, 0.4);
    grid-template-columns: repeat(2, 1fr);
    .sub-setting--width,
    .sub-setting--margin {
      display: none;
    }
  }
  .container {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
  .contentblock {
    padding-bottom: 0 !important;
  }
  & > .pimcore_tag_block:not(.pimcore_block_limitnotreached) {
    display: grid;
    grid-column-gap: $containerMarginDesktop;
  }
}

.largeteaser--image {
  figure {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.cols--2 {
  & > .pimcore_tag_block {
    grid-template-columns: repeat(2, 1fr);
  }
}

.cols--3 {
  & > .pimcore_tag_block {
    grid-template-columns: repeat(3, 1fr);
  }
}

.cols--4 {
  & > .pimcore_tag_block {
    grid-template-columns: repeat(4, 1fr);
  }
}


/* Backend Relation */

.backend-relation {
  position: relative;
  z-index: 99;
}

/* Backend Logo */

#pimcore_panel_tabs-body {
  background-size: 250px auto !important;
}

/* Iconblock */

@media (min-width: 768px) {
  .iconblock {
    display: block;
  }
}

